import React from "react";
import { useI18next } from "gatsby-plugin-react-i18next";
import { UserProfile } from "../interfaces"; 

interface NavProps {
  data?: UserProfile;
}

const LanguageSelector: React.FC<NavProps> = ({ data }) => {
  const { languages, changeLanguage, language: currentLanguage } = useI18next();

  if (!data?.menu) {
    return null;
  }

  return (
    <div className={styles.container}>
      <ul className={styles.buttonsWrapper}>
        {languages.map((lng) => (
          <li
            key={lng}
            className={`${styles.buttons} ${ styles.hover }
            ${lng === currentLanguage ? "bg-none border-none" : "bg-blueLight"}`}
            onClick={(e) => {
              e.preventDefault();
              changeLanguage(lng);
            }}
          >
            <div className={styles.text}>{lng}</div>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default LanguageSelector;

const styles = {
  container: "flex flex-row justify-center self-center",
  buttonsWrapper: "flex justify-evenly flex-row",
  buttons: "bg-blueDark  flex w-10 h-10 shrink-0 grow-0 rounded-full justify-center text-white cursor-pointer mx-1",
  text: "self-center capitalize",
  hover: "bg-primary hover:bg-blueLight hover:border-primary hover:border-2 hover:text-primary",
};
