import React, { useState, useEffect } from "react";
import { Trans, useTranslation } from "gatsby-plugin-react-i18next";
import { FiMenu, FiX } from "react-icons/fi";
import { FaArrowUp } from "react-icons/fa";

interface NavProps {
  data?: {
    menu: string[];
  };
}

const Navigation: React.FC<NavProps> = ({ data }) => {
  const { t } = useTranslation();
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [showBackToTop, setShowBackToTop] = useState(false);

  if (!data || !data.menu) {
    return null;
  }

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  const scrollToTop = () => {
    window.scrollTo({ top: 0, behavior: "smooth" });
  };

  useEffect(() => {
    const handleScroll = () => {
      if (window.scrollY > 100) {
        setShowBackToTop(true);
      } else {
        setShowBackToTop(false);
      }
    };
    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  return (
    <nav className={styles.container}>
      <button onClick={toggleMenu} className={styles.burgerButton}>
        {isMenuOpen ? <FiX className="text-white fixed right-4 z-[70] top-4" size={36} /> : <FiMenu className="text-orange mx-8" size={36} />}
      </button>

      <ul className={`${styles.buttonsWrapper} ${isMenuOpen ? styles.menuOpen : styles.menuClosed}`}>
        {data.menu.map((item: string, index: number) => (
          <li key={index} className={styles.buttonsContainer}>
            <button
              onClick={() => {
                document.getElementById(item)?.scrollIntoView({ behavior: "smooth" });
                setIsMenuOpen(!isMenuOpen)
              }}
              className={styles.buttons}>
              <Trans i18nKey={`menu.${item}`}>
                {t(`menu.${item}`)}
              </Trans>
            </button>
          </li>
        ))}
      </ul>

      {showBackToTop && (
        <button onClick={scrollToTop} className={styles.backToTopButton}>
          <FaArrowUp size={20} />
        </button>
      )}
    </nav>
  );
};

export default Navigation;

const styles = {
  container: "inline-block justify-between items-center bg-transparent sm:bg-orange px-4 py-2 mx-0 sm:mx-8 rounded-none sm:rounded-full relative z-10",
  burgerButton: "md:hidden block focus:outline-none mx-auto",
  buttonsWrapper: "flex flex-col md:flex-row justify-evenly items-center sm:w-[920px] w-full fixed sm:relative left-0 top-0 py-8 sm:py-0 gap-4 bg-orange sm:bg-transparent",
  buttonsContainer: "my-4 sm:my-0",
  buttons: "text-white px-4 py-0 hover:underline hover:text-orange",
  menuOpen: "block md:flex",
  menuClosed: "hidden md:flex",
  backToTopButton: "fixed bottom-4 right-4 bg-orange text-white p-2 rounded-full shadow-lg z-50",
};
