import React, { ReactNode, useState } from "react";
import { UserProfile } from "../interfaces";
import Navigation from "../components/nav";
import LanguageSelector from "../components/languageSelector";
import images from "../images";
import { useTranslation } from "gatsby-plugin-react-i18next";
import InstagramIcon from "@mui/icons-material/Instagram";
import FacebookIcon from "@mui/icons-material/Facebook";
import PhoneIcon from '@mui/icons-material/Phone';
import SidePanel from "../components/sidePanel";

interface LayoutProps {
  children: ReactNode;
  data?: UserProfile;
}

interface FooterData {
  copy: string;
}

/**
 * The main layout component. It renders the navigation, language selector, main content, side panel and footer.
 *
 * @param {LayoutProps} props The props for the component. It should include `children` and optionally `data`.
 * @returns {ReactNode} The JSX for the component.
 */
const Layout: React.FC<LayoutProps> = ({ children, data }) => {
  const { t } = useTranslation();
  const footerContent: { followUs: string; content: FooterData[] } = t("footer", { returnObjects: true }) || { followUs: "", content: [] };
  const currentYear = new Date().getFullYear();

  return (
    <>
      <div id={data?.menu[0]} className={styles.container}>
        <div className={styles.header}>
          <div>
            <img src={images.logo.src} alt={images.logo.alt} />
          </div>
          <Navigation data={data} />
          <LanguageSelector data={data} />
        </div>
        <div>{children}</div>
        <SidePanel />
      </div>


      <footer className={styles.footerContainer}>
        <div className="flex flex-col sm:flex-row">
          <div className="flex flex-col self-center p-4 sm:p-8 w-full sm:w-1/2">
            {footerContent.content?.map((item, index) => (
              <p key={index} className="py-2 text-sm">{item.copy}</p>
            ))}
          </div>
          <div className="flex flex-col self-center w-full sm:w-1/4">
            <img className="w-36 mx-auto" src={images.eco_friendly.src} alt={images.eco_friendly.alt} />
          </div>
          <div className="flex flex-col self-center w-full sm:w-1/4">
            <h3 className="mx-auto sm:mx-0 mt-4 sm:mt-0">{t("footer.followUs")}</h3>
            <div className="flex mt-2 mx-auto sm:mx-0">
              <a href={data?.instagram}>
                <InstagramIcon className="text-orange mr-4" fontSize="large" />
              </a>
              <a href={data?.facebook}>
                <FacebookIcon className="text-orange" fontSize="large" />
              </a>
            </div>
          </div>
        </div>
        <div className="mx-auto p-4">&copy; {currentYear} {data?.name}</div>
      </footer>
    </>
  );
};

export default Layout;

const styles = {
  container: "max-w-screen-xl mx-auto py-0 sm:py-8 px-4 sm:px-0",
  header: "flex flex-row justify-center items-center py-4 sm:py-0",
  footerContainer: "flex flex-col bg-darkGray text-lightGray"
};
