import React, { useState } from "react";
import { UserProfile } from "../interfaces";
import { useTranslation } from "gatsby-plugin-react-i18next";
import InstagramIcon from "@mui/icons-material/Instagram";
import FacebookIcon from "@mui/icons-material/Facebook";
import PhoneIcon from "@mui/icons-material/Phone";

interface SidePanelProps {
  data?: UserProfile;
}

const SidePanel: React.FC<SidePanelProps> = ({ data }) => {
  const { t } = useTranslation();

  const [isSidebarOpen, setIsSidebarOpen] = useState(false);

  return (
    <>
      <div
        className={`${styles.sidebar} ${
          isSidebarOpen ? styles.sidebarOpen : ""
        }`}
      >
        <div className="flex flex-col mx-auto space-y-4 mt-8 ml-4">
          <a
            href={data?.instagram}
            target="_blank"
            rel="noopener noreferrer"
            className="text-sm"
          >
            <InstagramIcon fontSize="large" className="text-orange" />
            Instagram
          </a>
          <a
            href={data?.facebook}
            target="_blank"
            rel="noopener noreferrer"
            className="text-sm"
          >
            <FacebookIcon fontSize="large" className="text-orange" />
            Facebook
          </a>
          <a
            href={`tel:${data?.phone}`}
            target="_blank"
            rel="noopener noreferrer"
            className="text-sm"
          >
            <PhoneIcon fontSize="large" className="text-orange" />
            {t("callUs")}
          </a>
        </div>
        <button
          className={styles.sidebarToggle}
          onClick={() => setIsSidebarOpen(!isSidebarOpen)}
        >
          {isSidebarOpen ? t("close") : t("connect")}
        </button>
      </div>
    </>
  );
};

export default SidePanel;

const styles = {
  sidebar:
    "fixed top-0 right-0 w-0 h-full bg-darkGray text-lightGray transition-width duration-300 z-50",
  sidebarOpen: "w-48",
  sidebarToggle:
    "absolute top-1/2 w-48 -left-28 transform rotate-[270deg] -translate-y-1/2 bg-orange text-white py-2 px-4 rounded cursor-pointer text-nowrap",
};
